

var waypoint = new Waypoint({
  element: document.getElementById('section'),
  handler: (direction) => {
    let nav = document.querySelectorAll('.nav__item');

    nav.forEach((item)=>{

      direction == 'down' && item.classList.add('black');
      direction == 'up' && item.classList.remove('black');   

    })
    
    
  }
})






